
import { Component, OnInit } from '@angular/core';
import { DataTransactionService } from './../../..//services/data-transaction/data-transaction.service';
import { Router } from '@angular/router';
import { ConstantService } from './../../../services/constant/constant.service';
import { ActionSheetController, AlertController, Platform } from '@ionic/angular';
import { Diagnostic } from '@ionic-native/diagnostic/ngx';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { NativeApiService } from '../../../services/native-api/native-api.service'
import { RouteProviderService } from 'src/app/services/route-provider/route-provider.service';
import { RestApiService } from 'src/app/services/rest-api.service';
import { AlertProviderService } from 'src/app/services/alert-provider/alert-provider.service';

@Component({
  selector: 'app-complete-profile',
  templateUrl: './complete-profile.page.html',
  styleUrls: ['./complete-profile.page.scss'],
  providers: [
    NativeApiService
  ]
})
export class CompleteProfilePage implements OnInit {

  Navbar = 'Personal';

  Model = {
    Phone: '',
    FullName: '',
    DisplayName: '',
    ProfileImage: '',
    Gender: 'Others',
    Resouses: {
      PhoneIcon: '',
      WorldPicture: '',
      ProfileImage: '',
      CameraImage: ''
    }
  }

  isDebug: boolean = false;
  AccessToken: '';
  ErrorMessage: string = '';

  constructor(
    public actionSheetController: ActionSheetController,
    public alertController: AlertController,
    private diagnostic: Diagnostic,
    private router: Router,
    private dataTransaction: DataTransactionService,
    private camera: Camera,
    private platform: Platform,
    private api: NativeApiService,
    private routeProvider: RouteProviderService,
    private restApi: RestApiService,
    private alert: AlertProviderService) {
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };

    if (platform.is('cordova')) {
      this.isDebug = false;
    }
    else this.isDebug = true;

    this.dataTransaction.getAccessToken().then(acc => {
      this.AccessToken = acc.toString();
    })


  }

  ngOnInit() {
    this.Model.Phone = "";
  }

  doNext() {
    if (!this.isDebug) {
      if (this.Model.DisplayName != '' && this.Model.FullName != '') {
        this.api.CompleteProfile(this.Navbar, this.Model.FullName, this.Model.DisplayName, this.Model.Gender, this.AccessToken)
          .then(response => {
            if (response.status == 200) {
              let result = this.api.converResponseToJson(response.data);
              if (result.ErrorCode == "0") {
                this.routeProvider.nextStep(result.Data.NextStep);
              } else {
                this.alert.showErrorMessageAlert(result.ErrorMessage);
              }
              this.router.navigate(['profile']);
            }


          }).catch(error => {
            this.alert.showErrorMessageAlert(error)
          })
      }
    } else {
      this.restApi.CompleteProfile(this.Navbar, this.Model.FullName, this.Model.DisplayName, this.Model.Gender, this.AccessToken)
        .subscribe(response => {
          let result = this.restApi.converResponseToJson(response);
          if (result.ErrorCode == "0") {
            this.routeProvider.nextStep(result.Data.NextStep);
          } else {
            this.ErrorMessage = result.ErrorMessage;
          }
          this.router.navigate(['profile']);
        })

    }


  }

  doPersonal() {
  }

  doBussines() {
  }

  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      header: 'Albums',
      buttons: [{
        text: 'Delete',
        role: 'destructive',
        icon: 'trash',
        handler: () => {
          this.Model.ProfileImage = ConstantService.icon_photo_camera;
        }
      }, {
        text: 'Camera',
        icon: 'camera',
        handler: () => {
          this.doTakePhoto(0)
        }
      }, {
        text: 'Gallery',
        icon: 'folder-open',
        handler: () => {
          this.doTakePhoto(1)
        }
      }, {
        text: 'Cancel',
        icon: 'close',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      }]
    });
    await actionSheet.present();
  }

  doTakePhoto(sourceType: number) {


    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      correctOrientation: true,
      sourceType: sourceType == 0 ? this.camera.PictureSourceType.CAMERA : this.camera.PictureSourceType.PHOTOLIBRARY,
    }

    this.diagnostic.isCameraAvailable().then((callback) => {
      this.camera.getPicture(options).then((imageData) => {
        let base64Image = 'data:image/jpeg;base64,' + imageData;
        this.Model.ProfileImage = base64Image;
        this.UploadPhoto();
      }, (err) => {
        this.presentAlert(err);
      });
    }).catch((err) => {
      this.presentAlert(err);
    })


  }

  async UploadPhoto() {
    if (!this.isDebug) {
      if (this.Model.ProfileImage != null) {
        await this.api.UploadProfilePhoto(this.Model.ProfileImage, this.AccessToken)
          .then(response => {
            if (response.status == 200) {
              let result = this.api.converResponseToJson(response.data);
              if (result.ErrorCode == "0") return true;
              else this.alert.showErrorMessageAlert(result.ErrorMessage)
            }
          }).catch(error => {
            this.alert.showErrorMessageAlert(error)
          })
      }
    }
  }

  async presentAlert(msg) {
    const alert = await this.alertController.create({
      header: 'Alert',
      subHeader: 'Subtitle',
      message: msg,
      buttons: ['OK']
    });

    await alert.present();
  }

}
